import $ from "jquery";
import listOpen from "./list-detail-open";
import mediapoint from "./mediapoint";
window.nps = {
  mediaquery: mediapoint
};
$(function() {
  $(window).on("mousewheel DOMMouseScroll", function() {
    $("html, body").stop();
  });

  $(".valueslider").flexslider({
    animation: "slide",
    easing: "easeinout",
    directionNav: true,
    controlNav: false,
    slideshow: false,
    animationSpeed: 500,
    keyboard: false,
    touch: true,
    start: function(slider) {
      $(".valueslider__counter").text(
        slider.currentSlide + 1 + "/" + slider.count
      );
    },
    after: function(slider) {
      $(".valueslider__counter").text(
        slider.currentSlide + 1 + "/" + slider.count
      );
    }
  });
  $("[data-gtag-event]").click(function() {
    if (!(typeof gtag === "undefined")) {
      var payload = $(this).data("gtag-event");
      payload = JSON.parse(JSON.stringify(payload));
      gtag("event", payload["name"], {
        event_category: payload["category"],
        event_label: payload["label"]
      });
    }
  });
  nps.reponsiveList = listOpen();
  nps.reponsiveList.init();
});
$(window).load(function() {
  nps.mediaquery();
});
